@import '../responsivesvalues.scss';

.navbar{
  display: flex;
  justify-content:space-between;
  padding-right: 3%;
  padding-left: 3%;
  position: fixed;
  z-index:999;
  background: rgba(255, 255, 255, 0.774);
  backdrop-filter: blur( 6px );
  box-sizing: border-box; 
  width: 100%;
  height: 150px;
  align-items: center;
  font-size: 30px;
  ul{
    display: flex;
  }
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    margin-left: 20px;
    margin-right: 30px;
  }
}

.logo-container{
  width: 100px;
  overflow: hidden;
}

.logo-img{
  object-fit: cover;
  width: 100%;
  height: 100%;
}

@media (max-width: $tablette) {
  .navbar{
    font-size: 20px;
  }
}


@media (max-width: $mobile ) {
  .navbar{
    height: 100px;
    &__links{
      display:none ; 
    }
  }
  .logo-container{
    width: 50px;
  }
  
}